.container{
    flex: 1;
    position: relative;
    /* border: solid white; */

}
.frequency-container{
    overflow: hidden;
    position: absolute;
    bottom: 50%;
    transform: translateY(50%);
    right: 30%;
    
}
.frequency{
    border-right: solid 3px white;
    padding-bottom: 6px;
    height: 150px;

}

.line{
    position: absolute;
    width: 100%;
    left: 70%;
    bottom: 50%;
    transform: translateY(50%);
    border: 3px solid #4E4CDD;
    
}

